<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>货主中心</BreadcrumbItem>
      <BreadcrumbItem>货主详情</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>货主信息</span>
          <Row slot="content" class="row">
              <Col span="2">货主</Col>
              <Col span="10">{{result.companyName}}</Col>
          </Row>
           <Row slot="content"  >
             <table class="table table-row">
                 <tr>
                     <td>联系人</td>
                     <td>联系人手机号</td>
                     <td>备注</td>
                 </tr>
                   <tr v-for="(item,index) in result.clientInfo" :key='index'>
                     <td>{{item.clientName}}</td>
                     <td>{{item.clientPhone}}</td>
                     <td>{{item.clientInfo}}</td>
                 </tr>
             </table>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">货主官网网址</Col>
              <Col span="8">{{result.companyOfficialUrl ? result.companyOfficialUrl:"-"}}</Col>
              <Col span="3">官网招标网址</Col>
              <Col span="2">{{result.companyBidUrl ? result.companyBidUrl:"-"}}</Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">货主简介</Col>
              <Col span="8">{{result.companyInfo ? result.companyInfo:"-"}}</Col>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
          <Row slot="content" class="row">
             <h3>业务状况</h3>
          </Row>
          <Row slot="content" class="row">
              <Col span="3">偏好航线</Col>
              <Col span="8">{{result.preferVoyage ? result.preferVoyage:"-"}}</Col>
          </Row>
          <Row slot="content" class="row">
              <Col span="3">偏好船型</Col>
              <Col span="8">{{result.preferShipType ? result.preferShipType:"-"}}</Col>
          </Row>
          <Row slot="content" class="row">
              <Col span="3">偏好货种</Col>
              <Col span="8">{{result.preferCargo ? result.preferCargo:"-"}}</Col>
          </Row>
          <Row slot="content" class="row">
              <Divider/>
          </Row>
           <Row slot="content" class="row">
             <h3>历史交易记录</h3>
          </Row>
          <Row slot="content"  >
             <table class="table table-row">
                 <tr>
                     <td>#</td>
                     <td>装货港</td>
                     <td>卸货港</td>
                     <td>货方运价</td>
                     <td>装货吨位</td>
                     <td>货种</td>
                     <td>订单创建时间</td>
                     <td>订单结束时间</td>
                 </tr>
                   <tr v-for="(item,index) in result.historyOrder" :key='index'>
                     <td>{{index+1}}</td>
                     <td>{{item.loadPorts}}</td>
                     <td>{{item.discPorts}}</td>
                     <td>{{item.freights}}</td>
                     <td>{{item.cargoTons}}</td>
                     <td>{{item.cargo}}</td>
                     <td>{{item.createTime}}</td>
                     <td>{{item.financeEndTime}}</td>
                 </tr>
             </table>
          </Row>
           <Row slot="content" class="row">
              <Button type="primary">
                <router-link :to="{
                 path: '/hzCenter/updatehz',query:{
                   code:this.$route.query.code
                }
              }">修改货主信息</router-link></Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:'',
                companyInfo:""
            }
        },
        methods:{
         async  huozhuDetail(){
              let data  = {
                code:this.$route.query.code
              }
             try{

                   let res = await this.$http.huozhuDetail(data)
                   if(0==res.data.status ){
                     this.result = res.data.result
                     this.companyInfo = res.data.result.companyInfo ? res.data.result.companyInfo :''

                   }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
           }
        },
        created(){
          this.huozhuDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
}
.table, tr ,td{
  border: 1px solid #e4e4ee;
  border-collapse: collapse;
  padding: 8px 30px;
  text-align: center;
}
a{
  color: #fff;
}
a:hover{
  color: #fff;
}
</style>



